<template>
  <div id="app-header">
    <h2 class="logo">
      <router-link :to="{ name: 'Home'}">MJ PORTFOLIO</router-link>
    </h2>
    <b-nav id="nav" pills>
      <b-nav-item to="/home" @click="selectedNav('home')">
        <ion-icon name="home"></ion-icon>
        Home
      </b-nav-item>
      <b-nav-item to="/about" @click="selectedNav('about')">
        <ion-icon name="person"></ion-icon>
        About
      </b-nav-item>
      <b-nav-item to="/work" @click="selectedNav('work')">
        <ion-icon name="filing"></ion-icon>
        Work
      </b-nav-item>
      <b-nav-item to="/contact" @click="selectedNav('contact')">
        <ion-icon name="mail"></ion-icon>
        Contact
      </b-nav-item>
    </b-nav>
    <span class="menu-toggle-btn" @click="toggleMenu">
      <span></span>
      <span></span>
      <span></span>
    </span>
    <div id="mobile-nav">
      <ul>
        <li @click="selectedNav('home')">
          <router-link :to="{ name: 'Home'}">
            <ion-icon name="home" :style="{ color: homeColor}"></ion-icon>
          </router-link>
        </li>
        <li @click="selectedNav('about')">
          <router-link :to="{ name: 'About'}">
            <ion-icon name="person" :style="{ color: aboutColor}"></ion-icon>
          </router-link>
        </li>
        <li @click="selectedNav('work')">
          <router-link :to="{ name: 'Work'}">
            <ion-icon name="filing" :style="{ color: workColor}"></ion-icon>
          </router-link>
        </li>
        <li @click="selectedNav('contact')">
          <router-link :to="{ name: 'Contact'}">
            <ion-icon name="mail" :style="{ color: contactColor}"></ion-icon>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'

  export default {
    name: "AppHeader",
    data() {
      return {
        homeColor: '',
        aboutColor: '',
        workColor: '',
        contactColor: '',
        showNav: false
      }
    },
    mounted() {
      $("#mobile-nav").hide();
      this.selectedNav(this.status);
    },
    methods: {
      toggleMenu() {
        $("#mobile-nav").animate({width: 'toggle'});
      },
      selectedNav(value) {
        if (value == 'home') {
          this.homeColor = '#3d3d3d';
          this.aboutColor = '#fff';
          this.workColor = '#fff';
          this.contactColor = '#fff';
        } else if (value == 'about') {
          this.homeColor = '#fff';
          this.aboutColor = '#3d3d3d';
          this.workColor = '#fff';
          this.contactColor = '#fff';
        } else if (value == 'work') {
          this.homeColor = '#fff';
          this.aboutColor = '#fff';
          this.workColor = '#3d3d3d';
          this.contactColor = '#fff';
        } else {
          this.homeColor = '#fff';
          this.aboutColor = '#fff';
          this.workColor = '#fff';
          this.contactColor = '#3d3d3d';
        }
      }
    },
    computed: {
      ...mapState(['status'])
    }
  }
</script>

<style scoped>
  .nav-pills .nav-link.active {
    background-color: #fff;
  }
</style>