<template>
  <div class="footer" :style="{ display: show }">
    <p>&copy; 2018. Minji Kim all rights reserved.</p>
  </div>
</template>

<script>
  import {mapState} from 'vuex'

  export default {
    name: "Footer",
    computed: {
      show() {
        if (this.status === 'tms' ||
          this.status === 'buyerPortal' ||
          this.status === 'fulfillment' ||
          this.status === 'shippingman' ||
          this.status === 'returnman') {
          return 'none';
        }
      },
      ...mapState(['status'])
    }
  }
</script>

